import React from 'react';
import "antd/dist/antd.css";

import { Button } from 'antd';
import { my_url } from './url.js';
import axios from 'axios';


export const ButtonDeleteFile = ({name, member, file, reeb}) => {
  
  const deleteFile =() =>{
    axios.get(`${my_url}delete`,{
      params:{
        member: member,
        name:name,
        file: file
      }
    }).then(res => {
     reeb(file);
    })
  };

   
     return (          
        <Button type="danger"
          size="small"
          icon="delete"
          onClick = {() => deleteFile()} 
        />   
     
      )
};       