import React from 'react';
import "antd/dist/antd.css";
import gql from 'graphql-tag';
import { useQuery } from '@apollo/react-hooks';
import {  Icon,  Tooltip,  Table,  Button, PageHeader } from 'antd';
import { Link } from 'react-router-dom';
import { my_url } from './url.js';



const  MEMBERS = gql`
  query{
    all_member{
      name
      age
      email
      gender
      password
      phone
    }
  }
`;

const columns = [
    {
      key: 'photo',
      title: 'photo',
      dataIndex: 'name',
      render: (name) => 
      <div style={{background: `url(${my_url}image/${name}.jpg) center/100% no-repeat`,width:80,height:80,margin:0}}></div>
    },
    {
      key: 'name',
      title: 'Name',
      dataIndex: 'name',      
    },
    {
      key: 'age',
      title: 'Age',
      dataIndex: 'age',
    },
    {
      key: 'phone',
      title: 'Phone',
      dataIndex: 'phone',
    },    
    {
      key: 'gender',
      title: 'gender',
      dataIndex: 'gender',
    },
    {
      key: 'email',
      title: 'email',
      dataIndex: 'email',
    },
    {
       key: 'action',
       title: 'action',
       dataIndex: '',
       align: 'center',
       render:() => (<><Button type="primary">edit</Button>
                        <Button type="danger">Delete</Button>
                    </>),
       width: '200px'

    },
   
  ];
export const Members = () =>{
    const {loading,data, err} = useQuery(MEMBERS,{
        fetchPolicy: 'network-only'
    });
    if ( loading )  return <h1 style={{position:'absolute',top:'25%',left:'45%'}}>                           
                              <Icon type="loading" spin />                          
                            </h1>;
    if ( err )  return <p>error</p>;
    return <>
  <Tooltip title="to main" placement="right">
		<Link to="/"> 
			<Button  style={{margin:20}} type="primary" >
				<Icon type="home" />
			</Button>
		</Link>
	</Tooltip>
  <PageHeader title= {`MEMBERS : ${data.all_member.length}`} style={{border:"1px solid #ccc",width:'90%',margin:'10px auto',}}>
    <Table dataSource={data.all_member} columns={columns} style={{padding:0}}>
    </Table>    
  </PageHeader>
    
    
  </>
};