import React from 'react';
import { Route, Switch, Redirect , Link} from 'react-router-dom';
import "antd/dist/antd.css";
import { SignInWindow } from './modal';
import  CreateAccount  from './create_account';
import EditAccount from './edit_account';
import { BrowserRouter } from 'react-router-dom';

import { InsideFolder } from './summer2019';
import { MainMenu } from './menu';
import { Tooltip, Icon, Button } from 'antd';
import { Members } from './members';



const  App = () => {

  const [member,set_member] = React.useState('');
 
  	return  (
	  <BrowserRouter>	
	   { member === 'admin' &&
	  			<Tooltip title="seen members" placement="right">
					<Link to="/members"> 
						<Button  style={{margin:20}} type="primary" >
							<Icon type="team" />
						</Button>
					</Link>
				</Tooltip>
		}			    		
		<Switch>			
			<Route exact path = "/" component = { () => ( 
				member === 'guest' || member === '' ? (
				<SignInWindow set={set_member} in_member={member} />
				) : ( 
				   <Redirect to = "/main"  name = { member } set = { set_member } />
				)
			)}/>		
			
			<Route path = "/inside/:member/:name" component = {(props) =>
			   <InsideFolder member={props.match.params.member} name={props.match.params.name} /> }
			/>
			
			<Route path = "/main" component = {() => (
			  member === 'guest' ? (
			  <Redirect to = "/" set={set_member} in_member={member} />
				) : ( 
				   <MainMenu name = { member } set = { set_member } />
				)
			  )}
			/>			
			<Route path = "/account_create" component={CreateAccount} />
			<Route path = "/account_edit" component={EditAccount} />
			<Route path = "/members" component={()=> <Members /> } />			
		</Switch>
		
	  </BrowserRouter>	  
	)
};
export { App };